    <div class="jianguan-content">
     <!--头部显示-->
      <div class="jianguan-top">
        <div class="jianguan-title">{{ pageText[0] }}</div>
        <div class="jianguan-top-nav">
          <div class="img"></div>
          <div class="jianguan-text">{{ pageText[1] }}</div>
        </div>
      </div>
