      <div class="jianguan-center-title">{{ pageText[2] }}</div>
      <div class="jianguan-items">
        <div class="jianguan-item" v-for="(item, index) in arrData" :key="index">
          <div class="jianguan-item-img"><img :src="item.img" alt=""></div>
          <div class="jianguan-item-title">{{ item.title }}</div>
          <div class="jianguan-item-text" v-html="item.text">{{}}</div>
        </div>
      </div>
     <!--底部交易 -->
      <div class="jianguan-bottom">
        <div class="jianguan-bottom-br"></div>
        <div class="jianguan-center-title">{{ pageText[3] }}</div>
        <div class="bottom-options flex-center">
          <div class="option1 pointer">{{ pageText[4] }}</div>
          <div class="option2 pointer">{{ pageText[5] }}</div>
        </div>
      </div>
    </div>
    <bottom></bottom>
  </div>
</template>

<script>
import Bottom from '../../components/bottom.vue'
import zhunbei from '../../components/zhunbei.vue'

export default {
  components: {zhunbei, Bottom},
  data() {
    return {
      pageText: window.language.jianguan.textArr,
      arrData: window.language.jianguan.arrData,
    }
  },
  methods: {}
}
</script>

<style scoped>
.jianguan-content {
  margin: 0 auto;
}

.jianguan-top {
  margin-top: 7rem;
  height: 100%;
  background: url("../../assets/jianguan/top.png") no-repeat center;
  background-size: 100% 100%
}

.jianguan-title {
  font-size: 4.5rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-weight: 600;
  color: #333333;
  line-height: 12.25rem;
  text-align: center;
}

.jianguan-top-nav {
  display: flex;
  justify-content: center;
}

.img {
  width: 700px;
  height: 438px;
  background: url("../../assets/jianguan/award.png") no-repeat center;
}

.jianguan-text {
  font-size: 16px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 400;
  color: #333333;
  width: 547px;
  height: 100px;
  line-height: 22.4px;
}

.jianguan-center-title {
  font-size: 2rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  margin: 1rem;
  font-weight: 600;
  color: #333333;
  line-height: 5.25rem;
  text-align: center;
}

.jianguan-items {
  display: flex;
  width: 80rem;
  /*margin-left:20rem;*/
  justify-content: space-around;
  flex-wrap: wrap;
  margin: 3rem auto;
  text-align: center;
}

.jianguan-item {
  padding: 0 0 0 1rem;
}

/*.jianguan-item + .jianguan-item {*/
/*  margin-top: 2.5rem;*/
/*}*/
.jianguan-item-title {
  /*margin: 1rem;*/
  font-size: 1.5rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-weight: 600;
  color: #000000;
  line-height: 3rem;
}

.jianguan-item-text {
  /*padding: 2.5rem 1.0625rem 2.3125rem 3.1875rem;*/
