  font-family: PingFang SC-Semibold, PingFang SC;
  font-weight: 600;
  color: #000000;
  line-height: 2rem;
  margin-bottom: 3rem;
  /*width: 81.63%;*/
  /*min-width: 81.63%;*/
}

.jianguan-item-img {
  width: 11rem;
  height: 70%;
}
.jianguan-bottom-br {
  margin:-1rem auto 3rem;
  height: .25rem;
  width: 5rem;
  background-color: #1677FF;
}

.bottom-options
{
  margin: 0 auto 2rem;
  width:38rem;
  text-align: center;
  display: flex;
  justify-content:space-between;
}
.option1
{
  padding-top:.875rem;
  width: 16.875rem;
  height:2.125rem;
  border-radius:4px;
  color: #FFFFFF ;
  background-color: #1677FF;
  border: none;
}
.option2
{
  padding-top:.875rem;
  width: 16.875rem;
  height:2.125rem;
  border-radius:4px;
  color: #FFFFFF ;
  background-color: #333333;
  border: none;
}
</style>



