}
.help-top-logo
{
  padding-top: 10rem;
  height:8.5rem;
  width:20.875rem;
  margin-top:7rem;

  background: url("../../assets/logo.png") no-repeat center;
  background-size: 90% 35%;

}
.help-top-title
{
  margin-top: 1.5rem;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-size:1.5rem;
  font-weight: 600;
  line-height:2rem;
  color:#FFFFFF;
}
.help-top-info
