<template>
  <div class="rujin-problem-item">
    <div class="rujin-problem-item-info flex-center">
      {{item.title}}
      <div style="padding:5px;" class="pointer" @click="show = !show">
        <i class="el-icon-arrow-down" :style="show ? 'transform:rotate(180deg);transition: .5s': ''"></i>
      </div>
    </div>
    <div class="rujin-problem-item-content" :style="show ? 'padding: 1rem;height:auto;transition: .5s': 'padding: 0rem 1rem;height:0;transition: .5s'">
      {{item.info}}
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      show: false
    }
  },
  props: [
    'item'
  ],
  methods: {
  }
}
</script>

<style scoped>
.rujin-problem-item {
  width: 48.13%;
  font-size: 1.125rem;
  font-family: PingFang SC-Regular, PingFang SC;
  font-weight: 400;
  color: #000000;
  line-height: 1.5rem;
}
.rujin-problem-item:nth-child(n + 3) {
  margin-top: .25rem;
}
.rujin-problem-item-info {
  padding: 1.1875rem 1rem;
  background: #C4C4C4;
  box-sizing: border-box;
  justify-content: space-between;
}
.rujin-problem-item-sanjiao {
  cursor: pointer;
  border-left: solid .3125rem transparent;
  border-right: solid .3125rem transparent;
  border-top: solid .3125rem #000;
}
.rujin-problem-item-content {
  background: #FFFFFF;
  height: 0;
  overflow: hidden;
  padding: 0 1rem 0;
}
</style>



