<template>
  <div class="jinshu-main">
   <!--顶部-->
   <div class="jinshu-top">
      <div class="jinshu-top-title">{{pageText[0]}}</div>
      <div class="jinshu-top-text">{{pageText[1]}}</div>
      <div class="jinshu-btns">
        <div class="jinshu-btn1 pointer">{{pageText[4]}}</div>
        <div class="jinshu-btn2 pointer">{{pageText[5]}}</div>
      </div>
     <div class="jinshu-top-about">{{pageText[6]}}<router-link to="" style="text-decoration:underline;font-size:1rem;color:#FFFFFF">{{pageText[7]}}</router-link></div>
     </div>
      <!--与WEEX交易贵金属的好处-->
       <div class="jinshu-advantages" v-for="(item,index) in arrData[0]" :key="index">
         <div class="jinshu-advantages-title">{{item.value}}</div>

       <div  class="jinshu-advantages-items">
         <div class="jinshu-advantages-item"   v-for="(val,key) in item.arrData" :key="key">
           <div class="jinshu-advantages-img" :style="'background:url(' + val.pic + ')no-repeat center;background-size:100% 100%'"></div>
           <div class="jinshu-advantages-item-title">{{val.title}}</div>
           <div class="jinshu-advantages-item-text">{{val.text}}</div>
         </div>
       </div>
       </div>
<!--    贵金属产品-->

    <div class="jinshu-product">
      <div class="jinshu-br"></div>
          <div v-for="(item,index) in arrData[1]" :key="index">
            <div class="jinshu-product-title">{{item.value}}</div>
            <table class="form">
              <tr>
                <th>{{item.title1}}</th>
                <th>{{item.title2}}</th>
              </tr>
                <tr  v-for="(val,key) in item.arrData" :key="key">
                  <td>{{val.title}}</td>
                  <td>{{val.text}}</td>
                </tr>
            </table>
            <div class="jinshu-product-btn">{{item.btn}}</div>
          </div>
    </div>
<!--    黄金期货日历-->
    <div class="jinshu-calendar">
      <div class="jinshu-br"></div>
      <div v-for="(item,index) in arrData[2]" :key="index">
        <div class="jinshu-product-title">{{item.value}}</div>
        <table class="form">
          <tr >
            <th  v-for="(val,key) in item.arrData1" :key="key">{{val.title}}</th>
          </tr>
            <tr  v-for="(val,key) in item.arrData" :key="key">
              <td>{{val.title}}</td>
              <td>{{val.contractTime}}</td>
              <td>{{val.id}}</td>
              <td>{{val.openDay}}</td>
              <td>{{val.endDay}}</td>
              <td>{{val.lastTime}}</td>
            </tr>

        </table>
      </div>
    </div>
    <div class="jinshu-medium">
      <div class="jinshu-medium-title">{{pageText[3]}}</div>
      <div class="jinshu-btns">
        <div class="jinshu-btn1 pointer">{{pageText[4]}}</div>
        <div class="jinshu-btn2 pointer">{{pageText[5]}}</div>
      </div>
      <div class="jinshu-top-about">{{pageText[6]}}<router-link to="" style="text-decoration:underline;font-size:1rem;color:#FFFFFF">{{pageText[7]}}</router-link></div>
    </div>
<!--  什么是贵金属交易  -->
     <div class="jinshu-what">
         <div class="jinshu-product-title">{{pageText[8]}}</div>
         <div class="text">{{pageText[9]}}</div>
     </div>
<!--    -->
<!--  为何与 WEEX 交易贵金属？  -->
    <div class="jinshu-why">
      <div class="jinshu-br"></div>
        <div class="jinshu-product-title">{{pageText[10]}}</div>
        <div class="text">{{pageText[11]}}</div>

    </div>
    <!-- 准备 -->
    <zhunbei>
      <template v-slot:default>
        <p>{{pageText[12]}}</p>
      </template>
    </zhunbei>
    <bottom>

    </bottom>
  </div>
</template>

<script>
import bottom from '../../components/bottom.vue'
export default {
  components: { bottom },
  data () {
    return {
      pageText: window.language.jinshu.textArr,
      arrData: window.language.jinshu.arrData,

  }
  },
  methods: {
  }
}
</script>

