  color: #FFFFFF;
  margin-top:5.25rem;
}
.jinshu-top-text {
  text-align: center;
  font-size:1.2rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight:600;
  color:#FFFFFF;
  line-height:1.5rem;
  margin: 2.5rem 0 2.5rem;
}
.jinshu-top-about {
  text-align: center;
  font-size:1rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 400;
  color:#FFFFFF;
  line-height:1.5rem;
  margin: 2.5rem 0 8rem;
}
.jinshu-btns
{
  margin: 0 auto;
  text-align: center;
}
.jinshu-btn1 {
  display: inline-block;
  padding-top:.875rem;
  width: 16.875rem;
  height:2.55rem;
  font-size: 1rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 500;
  color: #FFFFFF;
  background: #1677FF;
  border-radius: .25rem;
  opacity: 1;
}
.jinshu-btn2
{
  margin-left:5rem;
  padding-top:.875rem;
  width: 16.875rem;
  height:2.55rem;
  display: inline-block;
  font-size: 1rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 500;
  color: #FFFFFF;
  border: 2px solid #FFFFFF;
  border-radius: .25rem;
  opacity: 1;
}
.jinshu-advantages-img{
  width:4rem;
  height:4rem;
  margin:3rem auto;
}
.jinshu-advantages
{
  margin: 0 auto;
  text-align:center;
}
.jinshu-advantages-title
{
  font-size: 2rem;
  line-height: 2.5rem;
  font-weight: 600;
  color:#333333;
  margin-top:5rem;
}
.jinshu-advantages-items
{
  display: flex;
  justify-content:space-evenly;
}
.jinshu-advantages-item
{
  width:17rem;
}
.jinshu-advantages-item-title
{
  font-size: 1rem;
  font-weight: 600;
  font-family: PingFang SC-Medium, PingFang SC;
  line-height: 1.5rem;
  margin-bottom: 1rem;
}
.jinshu-advantages-item-text
{
  width:89%;
  font-size:.8rem;
  font-weight:400;
  font-family: PingFang SC-Medium, PingFang SC;
  line-height:2rem;

}
.jinshu-product-title{
  font-size: 2rem;
  font-weight:500;
  font-family: PingFang SC-Medium, PingFang SC;
  line-height:2.5rem;
  text-align: center;
  margin: 4rem auto 4rem;
}
.jinshu-product-btn
{
  margin: 2.5rem auto;
  border: 1px solid #333333;
