  font-size: 1rem;
  line-height: 1.5rem;
  width:66%;
  margin:0 auto;
  text-align: center;
}
th
{
  padding:.875rem;
  font-size: 1rem;
  height: 3rem;
  width: 10rem;
  text-align: center;
  background-color: #EEEEEE;
  border: 1px solid #FFFFFF;
}
td
{

  padding:.875rem;
  font-size: 1rem;
  text-align: center;
  border: 1px solid #FFFFFF;
  background-color: #F5F5F5;
}
