<template>
  <div class="bottom-main">
    <div class="bottom-item flex-center">
      <div class="bottom-left">
        <div class="bottom-logo"></div>
        <div class="bottom-btns">
          <div>
            <div class="bottom-btn" v-for="(item, key) in pageText[4].contact" :key="key">{{item.title}}</div>
          </div>

          <div>
            <div class="bottom-btn" v-for="(item, key) in pageText[4].private" :key="key">{{item.title}}</div>
          </div>
          <div>
            <div class="bottom-btn">{{pageText[1]}}</div>
            <div class="bottom-btn">{{pageText[2]}}</div>
          </div>
        </div>
      </div>
      <div class="bottom-right">
        <div class="all">
          <div class="bottom-right-option">
            <div class="bottom-right-about" v-for="(item, key) in pageText[4].aboutus" :key="key" >
              <router-link :to="item.pathUrl"  style="color: #FFFFFF;text-decoration: none">{{item.title}}</router-link>
            </div>
          </div>
          <div class="bottom-right-option" >
            <div class="bottom-right-about" v-for="(item, key) in pageText[4].content" :key="key" >
              <router-link :to="item.pathUrl" style="color: #FFFFFF;text-decoration: none">{{item.title}}</router-link>
            </div>
          </div>
          <div class="bottom-right-option">
            <div class="bottom-right-about" v-for="(item, key) in pageText[4].know" :key="key">
              <router-link :to="item.pathUrl"  style="color: #FFFFFF;text-decoration: none">{{item.title}}</router-link>
            </div>
          </div>
        </div>
        <div class="bottom-right-text-border">
          <div class="bottom-right-title">{{pageText[3]}}

          </div>
          <div class="bottom-right-text" v-for="(item, key) in pageText[0]" :key="key">
            {{item}}
          </div>
        </div>

      </div>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {

        pageText: window.language.bottom,
      }
    }
  }
</script>

<style scoped>
  .bottom-main {
    background: #24282D;
    position: absolute;
    background-size: 100% 100%;
    width: 100%;
  }

  .bottom-left {
    margin-top:2rem;
    padding-right:2.5rem;
  }

  .bottom-btns {
    height:19rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    font-size:.75rem;
  }

  .bottom-item {
    padding: 2rem 0;
    width: 75rem;
    margin: 0 auto;
    justify-content: space-between;
    align-items: flex-start;
  }

  .bottom-logo {
    width: 8.875rem;
    height: 3rem;
    background: url('../assets/logo.png') no-repeat center;
    background-size: 100% 100%;
    margin-bottom: 1.25rem;
  }

  .bottom-btn {
    /* width: 2rem;
     height: 4rem;*/
    cursor: pointer;
    color: #FFFFFF;
    line-height:2rem;
    font-weight: 400;

  }

  .bottom-right {
    display: flex;
    flex-direction: column;
    border-left:.3rem solid #1677FF;
    margin-top:2rem;
    width: 81%;
  }

  .all {

    margin-left:2.5rem;
    font-family: PingFang SC-Regular, PingFang SC;
    font-style: normal;
    font-weight: 400;
    font-size:.75rem;
    line-height:1rem;
    display: flex;
    justify-content: space-between;
    width:60%;

  }

  .bottom-right-about {
    font-size:.75rem;
    color: #FFFFFF;
    padding:.25rem;
    flex-direction: column;
  }

  .bottom-right-text-border {
    border-top:.125rem solid #333333;
    padding:.675rem 0;
    margin:3.5rem 2.5rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }

  .bottom-right-title {
    font-size:.75rem;
    font-weight: 600;
    margin:1.25rem 0 1.25rem 0;
    color: #FFFFFF;
  }

  .bottom-right-text {
    font-size: .75rem;
    font-family: PingFang SC-Regular, PingFang SC;
    font-weight: 400;
    color: #9E9E9E;

    line-height:1rem;
  }

  .bottom-right-text + .bottom-right-text {
    margin-top: 1.5625rem;
  }
  .bottom-copy-right-text + .bottom-copy-right-text {
    margin-left: 2rem;
  }
</style>



