      <!--邮箱-->
      <div class="lianxi-main-top-title">{{pageText[0]}}</div>
      <div class="lianxi-main-top-text">
        <div class="lianxi-yiwen-item"  v-for="(item, index) in arrData[1]" :key="index">
          <div class="lianxi-yiwen-item-title" style="list-style: initial;margin-left:-1.5rem;"><li>{{item.title}}</li></div>
        </div>
      </div>
    </div>

      <div class="map">
         <div>{{pageText[2]}}</div>
        <div class="svg">
        </div>
      </div>
    <bottom></bottom>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pageText: window.language.lianxi.textArr,
      arrData: window.language.lianxi.arrData,
      headerStyle: {
        background: '#BDBDBD',
        color: '#000000'
      }
    }
  },
  methods: {
  }
}
