  margin-top:7rem;
  height:600px;
}
.lianxi-about
{
  font-size:16px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 600;
  color: #FFFFFF;
  line-height: 3rem;
  margin-left:20.25rem;
  margin-top:1rem;
  margin-bottom: -3rem;
  border-bottom: 2px solid #FFFFFF;
  width:30rem;
}



.lianxi-main-top-title{
  font-size:28px;
  font-family: PingFang SC-Medium, PingFang SC;
  font-weight: 600;
  color: #FFFFFF;
  line-height:3rem;
  margin-left:20.25rem;
  margin-top:4rem;
  position: relative;
  padding: 0;
}
.lianxi-main-top-title::after{
 content:'';
  display: block;
  width:3.5%;
