  margin-left:3.25rem;
  margin-top: 1rem;
}

.lianxi-yiwen-item {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 0 300px;

}
.lianxi-yiwen-item-title {
  list-style: none;
  font-size:16px;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-weight: 600;
  line-height: 2rem;
}
