.lianxi-yiwen-item-text {
  font-size:16px;
  font-family: PingFang SC-Semibold, PingFang SC;
  font-weight: 600;
  color: #ffffff;
  line-height: 1.75rem;
  align-self:center;
  align-items: flex-end;
}
.map
{
  font-family: PingFang SC-Semibold, PingFang SC;
  width:100rem;
  margin:6rem auto 0;
  text-align: center;
  font-size: 2rem;
  line-height: 1.5rem;
  color: #24282d;
  font-weight: 400;
}
.svg
{
  padding:25rem 0;
  background:url("../../assets/lianxi/map.png") no-repeat center;
  background-size:100% 100%;
}
</style>



