<template>
  <div class="biaozhun-table">
    <div class="biaozhun-cell biaozhun-table-header flex-center">
      <div class="biaozhun-table-headerItem" v-for="(item, index) in tableData.header" :key="index" :style="'width: ' + parseInt(100 / tableData.header.length) + '%'">
        <div class="biaozhun-table-headerItem-text">{{item}}</div>
      </div>
    </div>
    <div class="biaozhun-cell biaozhun-table-content flex-center" v-for="(item, index) in tableData.content" :key="index">
      <div class="biaozhun-table-contentItem" v-for="(val, key) in item" :key="key" :style="'width: ' + parseInt(100 / tableData.header.length) + '%'">
        <div class="biaozhun-table-contentItem-text">{{val}}</div>
      </div>
    </div>
    <div class="biaozhun-cell biaozhun-table-btnItem flex-center">
      <div class="biaozhun-table-btnItems pointer"  v-for="(item, index) in tableData.btn" :key="index" :style="'width: ' + parseInt(100 / tableData.header.length) + '%'">
        <div class="biaozhun-table-btnItem-text">{{item}}</div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
    }
  },
  props: [
    'tableData'
  ],
  methods: {
  }
}
</script>

<style scoped>
.biaozhun-table-headerItem:nth-child(1) .biaozhun-table-headerItem-text, .biaozhun-table-btnItems:nth-child(1) .biaozhun-table-btnItem-text{
  background: transparent;
}
.biaozhun-cell {
  align-items: stretch;
  justify-content: space-between;
  font-size: 1.125rem;
  border-bottom: 1px solid #eeeeee;
}
.biaozhun-table-headerItem-text {
  width: calc(100% - .5rem);
  padding: 1.5rem;
  box-sizing: border-box;
  background: #26292D;
  border-radius: .25rem;
  color: #fff;
}
.biaozhun-table-contentItem-text, .biaozhun-table-btnItem-text {
  width: calc(100% - .5rem);
}
.biaozhun-table-contentItem {
  padding: 1rem;
  box-sizing: border-box;
}
.biaozhun-table-btnItems {
  padding: 1rem;
  box-sizing: border-box;
}
.biaozhun-table-btnItem-text {
  text-align: center;
  padding: .8125rem 1.375rem;
  box-sizing: border-box;
  background: #1677FF;
  font-size: 1rem;
  border-radius: .25rem;
  color: #fff;
}
</style>



