<template>
  <div>
  <div class="zhanghu-main">

    <!-- 顶部标签页 -->
      <div class="zhanghu-left">
        <div class="zhanghu-logo"></div>
        <div class="zhanghu-title">{{pageText[0]}}</div>
        <div class="zhanghu-text">{{pageText[1]}}</div>
      </div>
          <router-view/>
    <!-- 语言切换 -->
    <div class="top-langItem">
      <el-dropdown @command="loadPage">
        <div>
          <div class="top-langItem-logo"></div>
          <div v-show="true">
            <div>
              {{languageArr[1].text}}
            </div>
          </div>
          <div v-show="false">
            <div>
              {{languageArr[0].text}}
            </div>
          </div>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item  v-for="(item, index) in languageArr" :key="index" :command="item.text">
              <div class="top-languageArrItem flex-center">
                <div class="top-languageArrItem-logo" :style="'background:url(' + item.logo + ')no-repeat center;background-size:100% 100%'"></div>
                <div class="top-languageArrItem-text">{{item.text}}</div>
              </div>
            </el-dropdown-item>
          </el-dropdown-menu>
        </div>
      </el-dropdown>
    </div>


  </div>
    <div class="zhanghu-bottom">
      <div>{{pageText[2]}}</div>
      <div>{{pageText[3]}}</div>
      <div>{{pageText[4]}}</div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      pageText: window.language.zhanghu.textArr,
      languageArr: [
        {
          logo: 'https://fuhuigala.com/static/picture/en.jpg',
          text: 'English',
        },
        {
          logo: 'https://fuhuigala.com/static/picture/ch.jpg',
          text: '中文',
        }
      ]
    }
  },

  methods: {
    loadPage (data) {
      this.setMoneyUnit(data)
      location.reload()
    },
    reslangIcon () {
      let language = this.getLanguage()
      if (language === '中文') {
        return 'https://fuhuigala.com/static/picture/ch.jpg'
      } else if (language === 'English') {
        return 'https://fuhuigala.com/static/picture/en.jpg'
      }
    }
  }
}
</script>

<style scoped>
.zhanghu-main
{
  margin-top:-4.5rem;
  display: flex;
  justify-content: space-between;
}
.zhanghu-left
{
  width: 56.25rem;
  background:url("../assets/zhanghutop.jpg");
  background-size: 100% 100%;
}
.zhanghu-logo
{
  height: 3rem;
  width:9.75rem;
  margin-left: 2.5rem;
  margin-top: 2.5rem;
  background:url("../assets/logo.png");
  background-size: 100% 100%;
}
.zhanghu-title
{

  margin:18.75rem 4.3rem 0;
  font-family: PingFang SC-Medium, PingFang SC;
  font-size:3rem;
  font-weight: 600;
  line-height:4rem;
  color: #FFFFFF;
}
.zhanghu-text
{
  margin:2.5rem 4.3rem  26rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-size:1.5rem;
  font-weight: 600;
  line-height:2rem;
  color: #FFFFFF;
}
.zhanghu-bottom
{
  margin: 1.3rem 1.5rem;
  font-family: PingFang SC-Medium, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:2rem;
  color: #8B919A;

}
.el-dropdown-menu__item + .el-dropdown-menu__item {
  margin-top: 1.25rem
}
.top-langItem-logo {

  cursor: pointer;
  width: 34px;
  height: 34px;
  border-radius: 50%;
  background: url('../assets/lang.png') no-repeat center;
  background-size: 100% 100%;
}
.top-langItem
{

  margin-right:2rem;
  margin-top: 3rem;
}
</style>



