            <td>{{val.contractTime}}</td>
            <td>{{val.id}}</td>
            <td>{{val.openDay}}</td>
            <td>{{val.endDay}}</td>
            <td>{{val.lastTime}}</td>
          </tr>

        </table>
      </div>
    </div>
    <div class="zhishu-medium">
      <div class="zhishu-medium-title">{{pageText[3]}}</div>
      <div class="zhishu-btns">
        <div class="zhishu-btn1 pointer">{{pageText[4]}}</div>
        <div class="zhishu-btn2 pointer">{{pageText[5]}}</div>
      </div>
      <div class="zhishu-top-about">{{pageText[6]}}<router-link to="" style="text-decoration:underline;font-size:1rem;color:#FFFFFF">{{pageText[7]}}</router-link></div>
