<template>
  <div class="login-main">
    <div class="login-logo"></div>
    <div class="login-input">
      <el-input :placeholder=pageText[0] clearable></el-input>
      <el-input :placeholder=pageText[1] clearable></el-input>
    </div>
      <div class="login-medium">
        <el-checkbox>{{pageText[2]}}</el-checkbox>
        <router-link style="text-decoration:none;color:#333333;font-size: .875rem" :to=pageText[7]>{{pageText[3]}}</router-link>
      </div>
      <div class="login-bottom">
       <router-link class="login-btn" :to=pageText[5]>{{pageText[4]}}</router-link>
       <router-link class="login-btn" to="">{{pageText[6]}}</router-link>
      </div>
    </div>
</template>

<script>
export default {
  data () {
    return {
      pageText: window.language.login.textArr,
    }
  },
  methods: {
  }
}
</script>

<style scoped>
.login-main
{
  width: 30rem;
  height: 30rem;
  margin-top: 11.25rem;
  box-shadow: 0px 2px 15px rgba(0, 0, 0, 0.15);
  border-radius: 20px;
}
.login-logo
{
  margin:2rem auto 4rem;
  height: 3.875rem;
  width: 11.25rem;
  background:url("../../assets/logo1.png") no-repeat center;
  background-size: 100% 100%;
}
.login-input
{
  margin: 0 auto;
  width: 80%;
}
.el-input
{
  margin-bottom:2rem;
}
.login-medium
{
  margin: 0 auto;
  font-family: PingFang SC-Medium, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:1.3rem;
  display: flex;
  width:24rem;
  color:#333333;
  justify-content: space-between;
}
.login-bottom
{
  margin:3rem auto;
  width:24rem;
  display: flex;
  justify-content:space-between;
}
.login-btn
{
  text-decoration: none;
  width:10rem;
  padding:.7rem;
  border-radius: .5rem;
  border:.125rem solid #1677FF ;
  color:#1677FF;
  font-family: PingFang SC-Medium, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height: 1.3rem;
  text-align: center;
}
.login-btn:hover
{
  background:#1677FF;
  color:#FFFFFF;
}
</style>



