import Vue from 'vue'
import Router from 'vue-router'
// 首页
import home from '@/page/home'

import help from '@/page/qita/help'
import deal from '@/page/qita/deal'
import xuzhi from '@/page/qita/instructions'

import Android from '@/page/mt5/Android'
import IOS from '@/page/mt5/IOS'
import Web from '@/page/mt5/Web'
import pc from '@/page/mt5/pc'
import mt from '@/page/mt5/mt'

import lianxi from '@/page/guanyu/lianxi'
import jianguan from '@/page/guanyu/jianguan'
import falv from '@/page/guanyu/falv'
import select from '@/page/guanyu/select'
import companion from '@/page/guanyu/companion'
import notice from '@/page/guanyu/notice'

import zhanghu from '@/page/zhanghu'
import login from '@/page/zhanghu/login'
import register from '@/page/zhanghu/register'
import reset from '@/page/zhanghu/reset'


import xianggang from '@/page/shichang/xianggang'
import gupiao from '@/page/shichang/gupiao'
import zhishu from '@/page/shichang/zhishu'
import nengyuan from '@/page/shichang/nengyuan'
import jinshu from '@/page/shichang/jinshu'
import jijin from '@/page/shichang/jijin'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'home',
      component: home
    },
    {
      path: '/help',
      name: 'help',
      component: help
    },
    {
      path: '/deal',
      name: 'deal',
      component: deal
    },
    {
      path: '/xuzhi',
      name: 'xuzhi',
      component: xuzhi
    },
    {
      path: '/Android',
      name: 'Android',
      component: Android,
    },
    {
      path: '/IOS',
      name: 'IOS',
      component: IOS,
    },
    {
      path: '/pc',
      name: 'pc',
      component: pc,
    },
    {
      path: '/Web',
      name: 'Web',
      component: Web,
    },
    {
      path: '/mt',
      name: 'mt',
      component: mt,
    },
    {
      path: '/lianxi',
      name: 'lianxi',
      component: lianxi
    },
    {
      path: '/jianguan',
      name: 'jianguan',
      component: jianguan
    },
    {
      path: '/falv',
      name: 'falv',
      component: falv
    },
    {
      path: '/select',
      name: 'select',
      component: select
    },
    {
      path: '/companion',
      name: 'companion',
      component: companion
    },
    {
      path: '/notice',
      name: 'notice',
      component: notice
    },
    {
      path: '/zhanghu',
      name: 'zhanghu',
      component: zhanghu,
      redirect: '/login',
      children: [
        {
          path: '/zhanghu/login',
          name: 'login',
          component: login
        },
        {
          path: '/zhanghu/register',
          name: 'register',
          component: register
        },
        {
          path: '/zhanghu/reset',
          name: 'reset',
          component: reset
        }
      ]
    },
    {
      path: '/gupiao',
      name: 'gupiao',
      component: gupiao
    }, {
      path: '/xianggang',
      name: 'xianggang',
      component: xianggang
    }, {
      path: '/jinshu',
      name: 'jinshu',
      component: jinshu
    }, {
      path: '/nengyuan',
      name: 'nengyuan',
      component: nengyuan
    }, {
      path: '/jijin',
      name: 'jijin',
      component: jijin
    }, {
      path: '/zhishu',
      name: 'zhishu',
      component: zhishu
    }
  ]
})



