<template>
<div>
  <div class="notice-main">
    <div class="notice-title">{{pageText[0]}}</div>
    <div class="notice-text">{{pageText[1]}}</div>


    <div class="notice-table" v-for="(item,index) in arrData[0]" :key="index">
      <table border="0" style="width:100%;background-color: #ADADAD">
        <tr class="notice-th">
          <th>{{item.id}}</th>
          <th style="width:31.5rem">{{item.company}}</th>
          <th>{{item.date}}</th>
          <th>{{item.dividend}}</th>
        </tr>
        <tr class="notice-td"  v-for="(val,key) in item.arrData" :key="key">
          <td>{{val.id}}</td>
          <td>{{val.company}}</td>
          <td>{{val.date}}</td>
          <td>{{val.dividend}}</td>
        </tr>
      </table>
      <div class="notice-tr2" v-for="(item,index) in arrData[1]" :key="index">
        <table border="0" style="width:100%;background-color: #ADADAD">
          <tr class="notice-th">
            <th>{{item.id}}</th>
            <th style="width:31.5rem">{{item.company}}</th>
            <th>{{item.date}}</th>
            <th>{{item.dividend}}</th>
          </tr>

          <tr class="notice-td" v-for="(val,key) in item.arrData" :key="key">
            <td>{{val.id}}</td>
            <td>{{val.company}}</td>
            <td>{{val.date}}</td>
            <td>{{val.dividend}}</td>
          </tr>
        </table>
      </div>
    </div>
    <div class="notice-bottom">
      <div>{{pageText[2]}}</div>
      <div style="color:#1677FF;">{{pageText[3]}}</div>
      <div>{{pageText[4]}}</div>
      <div style="color:#1677FF;">{{pageText[5]}}</div>
      <div>{{pageText[6]}}</div>
    </div>
  </div>

  <bottom></bottom>
</div>
</template>

<script>
import bottom from '../../components/bottom.vue'
export default {
  components:{bottom},
  name: "notice",
  data()
  {
    return{
      pageText:window.language.notice.textArr,
      arrData:window.language.notice.arrData
    }
  }
}
</script>

<style scoped>
.notice-main
{
  margin-top: 8rem;
}
.notice-title
{
  margin: 0 auto 1.3rem;
  width:75rem;
  font-family: PingFang SC-Regular, PingFang SC;
  font-size:2rem;
  font-weight: 600;
  line-height:3rem;
  color: #333333;
}
.notice-text
{
  width:75rem;
  margin: 0 auto 1.3rem;
  font-family: PingFang SC-Regular, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:1.3rem;
}
.notice-table
{
  margin: 0 auto;
  width: 75rem;
}
.notice-th
{

  font-family: PingFang SC-Regular, PingFang SC;
  font-size:1rem;
  font-weight: 400;
  line-height:1.1rem;
  background-color:#F9F9F9;
}
.notice-td
{
  background-color: #FFFFFF;
  text-align: center;
}
.notice-tr2
{
  margin-top:4.4rem;

}
tr:nth-child(2n)
{
  background-color:#F9F9F9;
}
th,td
{
  font-size: 1rem;
  height: 3rem;
  color: #333333;
}
table
{
  margin: 0 auto;
}
.notice-bottom
{
  width:75rem;
  margin:2.5rem auto;
  font-family: PingFang SC;
  font-size:1rem;
  font-weight: 600;
  line-height:3rem;
  color: #000000;
}
</style>



