<template>
  <div class="picText_main">
    <div class="home-kh-item flex-center">
      <div class="home-kh-pic" :style="'background:url(' + pic + ')no-repeat center;background-size:100% 100%'"></div>
      <div class="home-kh-textItem" :style="'background: ' + background + ';padding:' + padding">
        <div class="home-kh-textItem-title" :style="'color:' + titleColor">{{title}}</div>
        <div v-html="text" class="home-kh-textItem-text" :style="'color:' + textColor"></div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {}
  },
  props: [
    'pic',
    'title',
    'text',
    'background',
    'padding',
    'titleColor',
    `textColor`
  ],
  methods: {
  }
}
</script>

<style scoped>
.home-kh-item {
  align-items: stretch;
}
.home-kh-pic, .home-kh-textItem {
  width: 50%;
}
.home-kh-textItem {
  padding: 8.0625rem 9.4375rem;
  background: #1F314F;
  box-sizing: border-box;
}
.home-kh-textItem-title {
  font-size: 2.5rem;
  font-weight: 500;
  color: #FFFFFF;
  line-height: 3.5rem;
  margin-bottom: .6875rem;
}
.home-kh-textItem-text {
  font-size: 1rem;
  font-weight: 400;
  color: #FFFFFF;
  line-height: 1.5rem;
}
</style>



